import styles from './NavProduction.module.scss';
import { RiQuestionLine } from 'react-icons/ri';

const NavProduction = () => {
  // TODO: items will have dynamic data from the backend
  return (
    <div className={styles.root}>
      <div className={styles.info}>
        <span className={styles.info__icon}>
          <RiQuestionLine />
        </span>
        <div>
          {/* If you need your order in a hurry please select Express production. Also allow for
          delivery time of 4-6 days. */}
          Choose how quickly you need us to work.
        </div>
      </div>

      <div className={styles.productionCategories}>
        <div className={styles.productionCategories__category}>
          <div className={styles.productionCategories__title}>
            Standard Production
            <span className={styles.productionCategories__infoIcon}>
              <RiQuestionLine />
            </span>
          </div>
          <p>Approx 3-4 weeks production + delivery time</p>
        </div>
      </div>
      <div className={styles.productionCategories}>
        <div className={styles.productionCategories__category}>
          <div className={styles.productionCategories__title}>
            Express
            <span className={styles.productionCategories__infoIcon}>
              <RiQuestionLine />
            </span>
          </div>
          <p>Approx 10-12 days production + delivery time (+ £29)</p>
        </div>
      </div>
      <div className={styles.productionCategories}>
        <div className={styles.productionCategories__category}>
          <div className={styles.productionCategories__title}>
            Super Express
            <span className={styles.productionCategories__infoIcon}>
              <RiQuestionLine />
            </span>
          </div>
          <p>As fast as 6-8 days production + delivery time (+ £45)</p>
        </div>
      </div>
    </div>
  );
};

export default NavProduction;
